.new-trip {
  position: relative;
  z-index: 2;
  margin-top: -30px;
  background-color: #d4d9eb;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.new-trip__inner {
  padding: 28px 25px 64px;
}

.new-trip__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.new-trip__title {
  margin: 0;
  font-size: 24px;
  font-weight: bold;
  line-height: 1;
  color: #1d2e5b;
}

.new-trip__steps {
  margin: 0;
  padding: 0;
  list-style: none;
}

.new-trip__step-item {
  margin-bottom: 10px;
  padding: 24px 20px 10px;
  background-color: #ffffff;
  border-radius: 10px;
}

.new-trip__step-item.arrival {
  padding-bottom: 22px;
}

.new-trip__step-item.entertainment {
  padding-top: 22px;
}

.new-trip__step-title {
  margin: 0 0 15px;
  font-size: 20px;
  font-weight: bold;
  line-height: 1;
  color: #1d2e5b;
}

.new-trip__step-text {
  margin: 0 0 20px;
}

.new-trip__next-step,
.new-trip__prev-step {
  display: block;
}

.new-trip__next-step {
  &:active {
    color: rgba(29, 46, 91, 0.3);
  }
}

.new-trip__btn-box .new-trip__next-step,
.new-trip__btn-box .directions-form__submit {
  margin-bottom: 12px;
}

.new-trip__steps .new-trip__step-marks {
  display: none;
}

.new-trip__step-marks.step-marks--dots {
  margin-right: 18px;
}

@media (min-width: $tablet-width) {
  .new-trip__inner {
    padding: 43px 75px 83px;
  }

  .new-trip__header {
    margin-bottom: 52px;
  }

  .new-trip__title {
    font-size: 36px;
    line-height: 1.39;
  }

  .new-trip__step-item {
    margin-bottom: 40px;
    padding: 44px 39px 52px 39px;
    border-radius: 30px;
  }

  .new-trip__step-item.arrival {
    padding-bottom: 55px;
  }

  .new-trip__step-item.entertainment {
    padding-top: 44px;
  }

  .new-trip__step-item.trip {
    margin-bottom: 51px;
  }

  .new-trip__step-item.entertainment .new-trip__step-title-wrapper {
    padding-bottom: 34px;
    margin-bottom: 26px;
  }

  .new-trip__step-title-wrapper {
    margin-bottom: 23px;
    padding-bottom: 40px;
    border-bottom: 1px solid #d6d9e6;
  }

  .new-trip__step-title {
    margin-bottom: 36px;
    font-size: 36px;
  }

  .new-trip__step-text {
    margin-bottom: 0;
    font-size: 20px;
    line-height: 1.2;
  }

  .new-trip__step-item.trip .new-trip__step-title-wrapper {
    margin-bottom: 39px;
    padding-bottom: 36px;
  }

  .new-trip__step-item.trip .new-trip__step-text {
    padding-right: 130px;
  }

  .new-trip__btn-box .new-trip__next-step,
  .new-trip__btn-box .directions-form__submit {
    margin-bottom: 0;
  }

  .new-trip__btn-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .new-trip__next-step {
    flex-basis: 285px;
  }

  .new-trip__prev-step {
    flex-basis: 179px;
    padding: 0;
  }

  .new-trip__next-step,
  .new-trip__prev-step {
    display: flex;
  }

  .new-trip__step-marks.step-marks--dots {
    margin-right: 2px;
  }
}

@media (min-width: $desktop-width) {
  .new-trip {
    margin-top: -60px;
    border-top-left-radius: 60px;
    border-top-right-radius: 60px;
  }

  .new-trip__inner {
    max-width: $desktop-width;
    margin-left: auto;
    margin-right: auto;
    padding: 46px 59px 132px 60px;
  }

  .new-trip__header {
    margin-bottom: 52px;
  }

  .new-trip__title {
    margin-left: 70px;
  }

  .new-trip__step-item {
    margin-bottom: 45px;
    padding: 95px 160px 105px;
    border-radius: 60px;
  }

  .new-trip__step-item.trip {
    margin-bottom: 45px;
    padding-bottom: 105px;
  }

  .new-trip__step-item.entertainment {
    margin-bottom: 0;
    padding-top: 95px;
  }

  .new-trip__step-item.arrival {
    padding: 98px 160px 104px;
  }

  .new-trip__step-item.trip .new-trip__step-title-wrapper {
    margin-bottom: 62px;
    padding-bottom: 55px;
  }

  .new-trip__step-item.entertainment .new-trip__step-title-wrapper {
    margin-bottom: 74px;
    padding-bottom: 55px;
  }

  .new-trip__step-title br {
    display: none;
  }

  .new-trip__step-title,
  .new-trip__step-text {
    max-width: 585px;
  }

  .new-trip__step-item.trip .new-trip__step-text {
    padding-right: 190px;
  }

  .new-trip__step-item.entertainment .new-trip__step-text {
    padding-right: 100px;
  }

  .new-trip__step-title-wrapper {
    position: relative;
    margin-bottom: 30px;
    padding-bottom: 54px;
  }

  .new-trip__header .new-trip__step-marks {
    display: none;
  }

  .new-trip__steps .new-trip__step-marks {
    display: block;
    position: absolute;
    top: 9px;
    right: 47px;
  }

  .new-trip__prev-step {
    max-width: 179px;
    margin-right: 4px;
  }
}
