.business-rates {
  width: 270px;
  min-height: 443px;
}

.business-rates__header {
  margin: 0 0 23px;
  font-size: 24px;
  line-height: 1;
  font-weight: bold;
  text-align: center;
  color: #ff8d30;
}

.business-rates__list {
  display: flex;
  flex-direction: column;
  margin: 0 0 33px;
  padding: 0;
  list-style: none;
}

.business-rates__item {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
  margin: 0;
  padding: 13px 0;
  font-weight: 300;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);

  &:first-child {
    border-top: 1px solid rgba(0, 0, 0, 0.05);
  }
}

@media (min-width: $tablet-width) {
  .business-rates {
    width: 688px;
  }

  .business-rates__header {
    margin-bottom: 50px;
    font-size: 45px;
  }

  .business-rates__list {
    margin-bottom: 52px;
  }

  .business-rates__item {
    flex-direction: row;
    justify-content: space-between;
    padding: 14px 0;
    font-size: 22px;
    line-height: 1.27;
  }
}

@media (min-width: $desktop-width) {
  .business-rates {
    width: 750px;
  }
}
